*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Roboto", Arial, sans-serif;
  color: #121212;
  background-color: #E5E5E5;
}

.modal {
  background-color: rgba(18, 18, 18, 0.5);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  padding: 5.6rem 9.5rem;
  background-color: #FFFFFF;
}
.modal__heading {
  font-size: 4.2rem;
  font-weight: 700;
}
.modal__text {
  font-size: 1.6rem;
}
.modal__exit-icon {
  position: absolute;
  top: 4.6rem;
  right: 4.6rem;
  font-size: 1.2rem;
  cursor: pointer;
}
.modal__clicked-info {
  font-weight: 700;
}
.modal__hidden {
  display: none;
}

.button {
  background-color: #E4C1AE;
  padding: 1.6rem 4rem;
  border: none;
  border-radius: 5rem;
  font-size: 1.6rem;
  font-weight: 700;
  color: #FFFFFF;
  cursor: pointer;
}

.content {
  max-width: 144rem;
  margin: 0 auto;
}
.content__img-container {
  width: 50%;
  overflow: hidden;
}
.content__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.content__heading {
  font-size: 4.2rem;
}
.content__text {
  margin-top: 2.4rem;
  margin-bottom: 3.2rem;
  font-size: 1.6rem;
  font-weight: 300;
}